import ReactGA from 'react-ga4';

const GA_MEASUREMENT_ID = import.meta.env.VITE_GA_ID;

export const initializeAnalytics = () => {
  if (GA_MEASUREMENT_ID) {
    ReactGA.initialize(GA_MEASUREMENT_ID);
    console.log('Google Analytics initialized with ID:', GA_MEASUREMENT_ID); // Optional: for confirmation
  } else {
    console.warn('Google Analytics Measurement ID is missing. Analytics not initialized.');
  }
};

export const logPageView = (path: string) => {
  if (GA_MEASUREMENT_ID) { // Only send if initialized
    ReactGA.send({ hitType: 'pageview', page: path });
  }
};

export const logEvent = (category: string, action: string, label?: string) => {
  if (GA_MEASUREMENT_ID) { // Only send if initialized
    ReactGA.event({
    category,
    action,
    label
    });
  }
};
