import React, { useState, useEffect, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Search, X } from 'lucide-react';
import { useNavigate } from 'react-router-dom';

interface CommandPaletteProps {
  isOpen: boolean;
  onClose: () => void;
}

interface Command {
  id: string;
  name: string;
  keywords: string[];
  action: () => void;
}

const CommandPalette: React.FC<CommandPaletteProps> = ({ isOpen, onClose }) => {
  const [searchQuery, setSearchQuery] = useState('');
  const [filteredCommands, setFilteredCommands] = useState<Command[]>([]);
  const [selectedIndex, setSelectedIndex] = useState(0);
  const searchInputRef = useRef<HTMLInputElement>(null);
  const navigate = useNavigate();

  const commands: Command[] = [
    {
      id: 'home',
      name: 'Go to Home',
      keywords: ['home', 'main', 'index'],
      action: () => navigate('/')
    },
    {
      id: 'about',
      name: 'Go to About',
      keywords: ['about', 'me', 'info'],
      action: () => navigate('/about')
    },
    {
      id: 'projects',
      name: 'Go to Projects',
      keywords: ['projects', 'work', 'portfolio'],
      action: () => navigate('/projects')
    },
    {
      id: 'blog',
      name: 'Go to Blog',
      keywords: ['blog', 'articles', 'posts'],
      action: () => navigate('/blog')
    },
    {
      id: 'contact',
      name: 'Go to Contact',
      keywords: ['contact', 'email', 'message'],
      action: () => navigate('/contact')
    },
    {
      id: 'resume',
      name: 'View Resume',
      keywords: ['resume', 'cv', 'download'],
      action: () => navigate('/about#resume')
    },
    {
      id: 'github',
      name: 'Open GitHub Profile',
      keywords: ['github', 'code', 'repository'],
      action: () => window.open('https://github.com/priyanshuchawda', '_blank')
    },
    {
      id: 'linkedin',
      name: 'Open LinkedIn Profile',
      keywords: ['linkedin', 'social', 'network'],
      action: () => window.open('https://linkedin.com/in/priyanshuchawda', '_blank')
    },
    {
      id: 'darkmode',
      name: 'Toggle Dark Mode',
      keywords: ['dark', 'light', 'theme', 'mode'],
      action: () => {
        document.documentElement.classList.toggle('dark');
        localStorage.setItem('theme', document.documentElement.classList.contains('dark') ? 'dark' : 'light');
      }
    }
  ];

  useEffect(() => {
    if (isOpen && searchInputRef.current) {
      searchInputRef.current.focus();
    }
  }, [isOpen]);

  useEffect(() => {
    if (searchQuery.trim() === '') {
      setFilteredCommands(commands);
    } else {
      const query = searchQuery.toLowerCase();
      const filtered = commands.filter(command => {
        return (
          command.name.toLowerCase().includes(query) ||
          command.keywords.some(keyword => keyword.toLowerCase().includes(query))
        );
      });
      setFilteredCommands(filtered);
    }
    setSelectedIndex(0);
  }, [searchQuery]);

  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (!isOpen) return;

      switch (e.key) {
        case 'ArrowDown':
          e.preventDefault();
          setSelectedIndex(prev => (prev + 1) % filteredCommands.length);
          break;
        case 'ArrowUp':
          e.preventDefault();
          setSelectedIndex(prev => (prev - 1 + filteredCommands.length) % filteredCommands.length);
          break;
        case 'Enter':
          e.preventDefault();
          if (filteredCommands[selectedIndex]) {
            executeCommand(filteredCommands[selectedIndex]);
          }
          break;
        case 'Escape':
          e.preventDefault();
          onClose();
          break;
      }
    };

    document.addEventListener('keydown', handleKeyDown);
    return () => document.removeEventListener('keydown', handleKeyDown);
  }, [isOpen, filteredCommands, selectedIndex, onClose]);

  const executeCommand = (command: Command) => {
    command.action();
    onClose();
  };

  // Click outside to close
  const paletteRef = useRef<HTMLDivElement>(null);
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (paletteRef.current && !paletteRef.current.contains(event.target as Node)) {
        onClose();
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, [onClose]);

  return (
    <AnimatePresence>
      {isOpen && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm z-50 flex items-start justify-center pt-[20vh]">
          <motion.div
            ref={paletteRef}
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.2 }}
            className="w-full max-w-xl bg-white dark:bg-gray-800 rounded-lg shadow-2xl overflow-hidden"
          >
            <div className="p-4 border-b border-gray-200 dark:border-gray-700 flex items-center">
              <Search className="w-5 h-5 text-gray-500 dark:text-gray-400 mr-3" />
              <input
                ref={searchInputRef}
                type="text"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                placeholder="Search commands..."
                className="flex-grow bg-transparent border-none outline-none text-gray-900 dark:text-white placeholder-gray-500 dark:placeholder-gray-400"
                autoComplete="off"
              />
              <button 
                onClick={onClose}
                className="p-1 rounded-full text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200"
                aria-label="Close command palette"
              >
                <X className="w-5 h-5" />
              </button>
            </div>
            
            <div className="max-h-80 overflow-y-auto">
              {filteredCommands.length > 0 ? (
                <ul>
                  {filteredCommands.map((command, index) => (
                    <li key={command.id}>
                      <button
                        className={`w-full text-left px-4 py-3 flex items-center ${
                          index === selectedIndex
                            ? 'bg-blue-50 dark:bg-blue-900/20'
                            : 'hover:bg-gray-50 dark:hover:bg-gray-700/50'
                        }`}
                        onClick={() => executeCommand(command)}
                        onMouseEnter={() => setSelectedIndex(index)}
                      >
                        <span className={`font-medium ${
                          index === selectedIndex
                            ? 'text-blue-600 dark:text-blue-400'
                            : 'text-gray-900 dark:text-white'
                        }`}>
                          {command.name}
                        </span>
                      </button>
                    </li>
                  ))}
                </ul>
              ) : (
                <div className="px-4 py-6 text-center text-gray-500 dark:text-gray-400">
                  No commands found for "{searchQuery}"
                </div>
              )}
            </div>
            
            <div className="px-4 py-3 bg-gray-50 dark:bg-gray-700/50 text-xs text-gray-500 dark:text-gray-400 flex justify-between">
              <div>
                <span className="mr-2">
                  <kbd className="px-2 py-1 bg-white dark:bg-gray-800 rounded border border-gray-200 dark:border-gray-600">â</kbd>
                  <kbd className="ml-1 px-2 py-1 bg-white dark:bg-gray-800 rounded border border-gray-200 dark:border-gray-600">â</kbd>
                  to navigate
                </span>
                <span>
                  <kbd className="px-2 py-1 bg-white dark:bg-gray-800 rounded border border-gray-200 dark:border-gray-600">Enter</kbd>
                  to select
                </span>
              </div>
              <div>
                <span>
                  <kbd className="px-2 py-1 bg-white dark:bg-gray-800 rounded border border-gray-200 dark:border-gray-600">Esc</kbd>
                  to close
                </span>
              </div>
            </div>
          </motion.div>
        </div>
      )}
    </AnimatePresence>
  );
};

export default CommandPalette;