import React, { useEffect, useState, useCallback } from 'react';
import { Outlet, useLocation } from 'react-router-dom';
import Navbar from './Navbar';
import Footer from './Footer';
import Sidebar from './Sidebar';
import CommandPalette from '../ui/CommandPalette';
import ScrollToTop from '../ui/ScrollToTop';
import { logPageView } from '../../lib/analytics'; // Assuming this path is correct

const MainLayout: React.FC = () => {
  // --- State ---
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [isCommandPaletteOpen, setIsCommandPaletteOpen] = useState(false);

  // --- Hooks ---
  const location = useLocation();

  // --- Callbacks ---
  const toggleSidebar = useCallback(() => {
    setIsSidebarOpen(prev => !prev);
  }, []);

  const closeSidebar = useCallback(() => {
    setIsSidebarOpen(false);
  }, []);

  const toggleCommandPalette = useCallback(() => {
    setIsCommandPaletteOpen(prev => !prev);
  }, []);

  // --- Effects ---
  // Log page views and close sidebar on navigation
  useEffect(() => {
    logPageView(location.pathname + location.search);
    closeSidebar();
    // REMOVED the eslint-disable comment here as it's no longer needed
  }, [location, closeSidebar]); // Dependencies are correct

  // Add keyboard shortcut listener for Command Palette
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === '/' && (e.metaKey || e.ctrlKey)) {
        e.preventDefault();
        toggleCommandPalette();
      }
      if (e.key === 'Escape' && isCommandPaletteOpen) {
         toggleCommandPalette();
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => {
      window.removeEventListener('keydown', handleKeyDown);
    };
  }, [toggleCommandPalette, isCommandPaletteOpen]);


  return (
    <div className="flex min-h-screen flex-col bg-gray-50 dark:bg-gray-900 transition-colors duration-200 ease-in-out overflow-x-hidden">
      {/* Navbar receives toggle functions with names it expects */}
      <Navbar
        // Changed back from onToggleSidebar/onToggleCommandPalette
        toggleSidebar={toggleSidebar}
        toggleCommandPalette={toggleCommandPalette}
      />

      {/* Sidebar receives state and the onClose function */}
      {/* Keep onClose here, but ensure SidebarProps in Sidebar.tsx expects 'onClose' */}
      <Sidebar isOpen={isSidebarOpen} onClose={toggleSidebar} />

      {/* Command Palette receives state and close function */}
      <CommandPalette isOpen={isCommandPaletteOpen} onClose={toggleCommandPalette} />

      <main className="flex-grow w-full">
        <Outlet />
      </main>

      <Footer />
      <ScrollToTop />
    </div>
  );
};

export default MainLayout;