import React from 'react';
import { NavLink } from 'react-router-dom';
import { X } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import { NavItem } from '../../types'; // Assuming this path and type are correct
import { socialLinks } from '../../data/socialLinks'; // Assuming this path is correct
import SocialIcons from '../ui/SocialIcons';

// Keep nav items here or move to a dedicated data file (e.g., src/data/navigation.ts)
const navItems: NavItem[] = [
  { title: 'Home', path: '/' },
  { title: 'About', path: '/about' },
  { title: 'Projects', path: '/projects' },
  { title: 'Blog', path: '/blog' },
];

interface SidebarProps {
  isOpen: boolean;
  onClose: () => void; // Function to close the sidebar
}

// Destructure 'onClose' instead of 'toggleSidebar'
const Sidebar: React.FC<SidebarProps> = ({ isOpen, onClose }) => {

  // Handle clicks on the backdrop overlay
  const handleOverlayClick = (e: React.MouseEvent<HTMLDivElement>) => {
    // Ensure the click is directly on the overlay, not on its children (the sidebar panel)
    if (e.target === e.currentTarget) {
      onClose(); // Use onClose here
    }
  };

  // Define animation variants for clarity (optional but good practice)
  const backdropVariants = {
    open: { opacity: 1 },
    closed: { opacity: 0 },
  };

  const sidebarVariants = {
    open: { x: 0 },
    closed: { x: '100%' },
  };

  return (
    // AnimatePresence handles the mounting/unmounting animation
    <AnimatePresence>
      {isOpen && (
        <>
          {/* Overlay */}
          <motion.div
            key="backdrop" // Add key for AnimatePresence tracking
            variants={backdropVariants}
            initial="closed"
            animate="open"
            exit="closed"
            transition={{ duration: 0.2, ease: "easeInOut" }}
            className="fixed inset-0 bg-black/60 backdrop-blur-sm z-40 md:hidden" // Slightly darker overlay
            onClick={handleOverlayClick}
            aria-hidden="true" // Hide decorative overlay from screen readers
          />

          {/* Sidebar Panel */}
          <motion.div
            key="sidebar" // Add key for AnimatePresence tracking
            variants={sidebarVariants}
            initial="closed"
            animate="open"
            exit="closed"
            transition={{ type: 'tween', duration: 0.3, ease: "easeInOut" }}
            className="fixed top-0 right-0 h-full w-64 sm:w-72 bg-white dark:bg-gray-800 shadow-xl z-50 md:hidden flex flex-col" // Added flex flex-col here
            role="dialog" // Role for accessibility
            aria-modal="true" // Indicates it's a modal dialog
            aria-label="Main menu" // Label for the dialog
          >
            {/* Header */}
            <div className="flex justify-between items-center p-4 border-b border-gray-200 dark:border-gray-700 flex-shrink-0">
              <span className="text-lg font-semibold text-gray-900 dark:text-white">Menu</span>
              <button
                onClick={onClose} // Use onClose here
                className="p-2 rounded-md text-gray-600 dark:text-gray-300 hover:bg-gray-200 dark:hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 dark:focus:ring-offset-gray-800 transition-colors"
                aria-label="Close menu"
              >
                <X className="w-5 h-5" />
              </button>
            </div>

            {/* Navigation */}
            <nav className="flex-grow overflow-y-auto p-4">
              <ul className="space-y-1"> {/* Slightly reduced space */}
                {navItems.map((item) => (
                  <li key={item.path}>
                    <NavLink
                      to={item.path}
                      onClick={onClose} // Use onClose here to close sidebar on navigation
                      className={({ isActive }) => `
                        block px-3 py-2 rounded-md text-base font-medium transition-colors duration-150 ease-in-out
                        ${isActive
                          ? 'bg-blue-100 dark:bg-blue-900/50 text-blue-700 dark:text-blue-300' // Adjusted active colors
                          : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700 hover:text-gray-900 dark:hover:text-white'
                        }
                      `}
                    >
                      {item.title}
                    </NavLink>
                  </li>
                ))}
              </ul>
            </nav>

            {/* Footer / Social Links */}
            <div className="p-4 border-t border-gray-200 dark:border-gray-700 flex-shrink-0">
              <SocialIcons links={socialLinks} className="flex justify-center space-x-5" /> {/* Increased spacing */}
            </div>

          </motion.div>
        </>
      )}
    </AnimatePresence>
  );
};

export default Sidebar;
